import './index.css';
import {React, useState} from 'react';

function App() {
  const[count, setCount] = useState(0);

  const countPlus = () =>{
    setCount(count + 1);
  }

  const countMinus = () =>{
    setCount(count - 1);
  }

  const countSqrt = () =>{
    setCount(count * 2);
  }
  return (
    <div className="App">
      <div>
        <h2>Счетчик:</h2>
        <h1>{count}</h1>
        <button onClick={countMinus} className="minus">- Минус</button>
        <button onClick={countPlus} className="plus">Плюс +</button>
        <button onClick={countSqrt} className="sqrt">Умножить *2</button>
      </div>
    </div>
  );
}

export default App;
